import { React, useState, useRef, useEffect, useCallback } from "react";
import computeIou from "./GetIou";
import axios from "axios";
import "./DrawCanvas.css";

function DrawCanvas() {
  const canvasRef = useRef(null); // 
  const canvasRef2 = useRef(null); // 

  const [ctx, setCtx] = useState(null); //  current
  const [ctx2, setCtx2] = useState(null); //  current

  const [boxColor, setBoxColor] = useState([]); //annotation color
  const [pos, setPos] = useState([]); //   
  const [bBox, setbBox] = useState([]); // bbox 
  const [isDraw, setIsDraw] = useState(false); //  
  const [jsonData, setJsonData] = useState(null); //json 
  const [mode, setMode] = useState(-1); //Iou  annotation ID
  const [iou, setIou] = useState(0); // IOU

  const [drawBbox, setDrawBbox] = useState();
  const [storedBox, setStoredBox] = useState([]);
  const [storedIou, setStoredIou] = useState([]); // IOU

  //  
  const getCanvas = () => {
    const canvas = canvasRef.current;
    canvas.width = jsonData.images[0].width;
    canvas.height = jsonData.images[0].height;
    const context = canvas.getContext("2d");
    setCtx(context);
  };

  //  
  const getBackCanvas = (drawImg) => {
    const canvas = canvasRef2.current;
    canvas.width = jsonData.images[0].width;
    canvas.height = jsonData.images[0].height;
    const context = canvas.getContext("2d");
    drawImg(context);
    context.fillRect(0, 0, 1000, 1000);
    setCtx2(context);
  };

  //jsondata, bbox  
  const getJsonData = async () => {
    await axios
      .get(
        "/json_data/labeling_data_4-1/IMG_0005049_umbrella(umbrella)_(4_1).json" //json   ?  api    
      )
      .then((res) => {
        setJsonData(res.data);
        if (res.data.annotations.length <= bBox.length) {
          return;
        }
        res.data.annotations.map(
          (
            item //bbox  
          ) =>
            setbBox((current) => {
              return [...current, item.bbox];
            })
        );
      });
  };

  //background  
  const drawImg = (context) => {
    if (jsonData) {
      const image = new Image();
      image.src = require("../public/json_data/original_data/" +
        jsonData.images[0].file_name); // 

      image.onload = () => {
        context.drawImage(
          image,
          0,
          0,
          jsonData.images[0].width,
          jsonData.images[0].height
        );
      };
    }
  };

  //bbox    
  const getBoxColor = () => {
    let r = Math.round(Math.random() * 255);
    let g = Math.round(Math.random() * 255);
    let b = Math.round(Math.random() * 255);
    setBoxColor((cur) => [...cur, `rgba(${r},${g},${b},0.5)`]);
  };

  //  
  useEffect(() => {
    if (jsonData === null && bBox.length === 0) {
      getJsonData();
    }
  }, [jsonData, bBox]);

  //canvas , storediou  boxcolor  
  useEffect(() => {
    if (jsonData !== null) {
      getCanvas();
      getBackCanvas(drawImg);
      if (boxColor.length < bBox.length) {
        for (let i = 0; i < bBox.length; i++) {
          getBoxColor();
          setStoredIou((cur) => [...cur, 0]);
          setStoredBox((cur) => [...cur, 0]);
        }
      }
    }
  }, [jsonData]);

  useEffect(() => {
    if (jsonData !== null) {
      getCanvas();
      getBackCanvas(drawImg);
    }
  }, []);

  //mode   IOU 0 
  useEffect(() => setIou(0), [mode]);

  function drawStart(e) {
    //
    console.log(bBox);
    setIsDraw(true);
    setPos([
      e.clientX - canvasRef.current.offsetLeft,
      e.clientY - canvasRef.current.offsetTop,
    ]);
  }

  function drawSquare(e) {
    //   
    if (!isDraw) return;
    ctx.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);
    ctx.fillStyle = "rgba(0,0,250,0.5)";
    ctx.strokeStyle = "blue";
    let currentX = e.clientX - canvasRef.current.offsetLeft;
    let currentY = e.clientY - canvasRef.current.offsetTop;
    ctx.strokeRect(pos[0], pos[1], currentX - pos[0], currentY - pos[1]);
    ctx.fillRect(pos[0], pos[1], currentX - pos[0], currentY - pos[1]);
  }

  function drawEnd(e) {
    // 
    setIsDraw(false);
    if (mode !== -1) {
      // iou 
      let currentX = e.clientX - canvasRef.current.offsetLeft;
      let currentY = e.clientY - canvasRef.current.offsetTop;
      setDrawBbox({
        x: pos[0],
        y: pos[1],
        w: currentX - pos[0],
        h: currentY - pos[1],
      });
      const iou = computeIou(
        {
          x: pos[0],
          y: pos[1],
          w: currentX - pos[0],
          h: currentY - pos[1],
        },
        {
          x: bBox[mode][0],
          y: bBox[mode][1],
          w: bBox[mode][2],
          h: bBox[mode][3],
        }
      );
      setIou(iou);
    }
  }

  function getIou(index) {
    if (index !== mode) {
      return 0;
    } else {
      return iou;
    }
  }

  return (
    <div>
      {jsonData === null ? (
        <div>Loading...</div>
      ) : (
        <div
          className="container"
          style={{
            display: "flex",
            flexDirection: "column",
            width: jsonData.images[0].width,
            height: jsonData.images[0].height,
          }}
        >
          <canvas className="img" ref={canvasRef2} />
          <canvas
            className="canvas"
            ref={canvasRef}
            onMouseDown={drawStart}
            onMouseUp={drawEnd}
            onMouseMove={drawSquare}
          />
          {bBox.map(
            (
              item,
              index //bbox 
            ) => (
              <div
                style={{
                  position: "absolute",
                  backgroundColor: boxColor[index],
                  left: item[0],
                  top: item[1],
                  width: item[2],
                  height: item[3],
                  zIndex: "2",
                }}
              ></div>
            )
          )}
          {jsonData.annotations.map((item, index) => (
            <div //annotation   
              style={{
                position: "relative",
                fontSize: 30,
                borderStyle: "solid",
                borderColor: boxColor[index],
                width: 400,
                left: jsonData.images[0].width,
                whiteSpace: "pre-line",
              }}
              onClick={() => setMode(index)}
            >
              Annotations ID:{item.id}
              {"\n"}Object Class:
              {"\n"} IOU: {getIou(index)}
              {"\n"} IOU:{storedIou[index]}
              {"\n"}
              <input
                type="button"
                value="IOU "
                onClick={() => {
                  let copy = storedIou;
                  copy[index] = iou;
                  let copy2 = storedBox;
                  copy2[index] = drawBbox;
                  setStoredIou(copy);
                  setStoredBox(copy2);
                  console.log(copy2);
                }}
                style={{ fontSize: 30 }}
              />
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default DrawCanvas;
